/* Import Raleway font to match other pages */
@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;500;600;700&display=swap');

.services-page {
  font-family: 'Raleway', sans-serif;
  color: #333;
  line-height: 1.6;
}

.services-hero {
  position: relative;
  height: 300px;
  background-image: url('../images/services.jpg');
  background-size: cover;
  background-position: center;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 2rem;
}

/* Green gradient fallback if image isn't available */
.services-hero:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #4CAF50, #2E7D32);
  z-index: -1;
}

.hero-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: -1;
}

.services-hero h1 {
  font-size: 3rem;
  margin-bottom: 20px;
  font-family: 'Raleway', sans-serif;
  font-weight: 700;
  color: #e1ffe2;
}

.services-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem 1rem 4rem;
}

.services-header {
  text-align: center;
  margin-bottom: 3rem;
  padding-bottom: 1rem;
  border-bottom: 2px solid #e0e0e0;
}

.services-header h1 {
  font-size: 2.5rem;
  color: #2c6e49;
  margin-bottom: 0.5rem;
}

.services-section {
  margin-bottom: 4rem;
}

.services-section h2 {
  font-size: 2rem;
  color: #2E7D32;
  margin-bottom: 1.5rem;
  position: relative;
  padding-bottom: 10px;
}

.services-section h2:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 60px;
  height: 3px;
  background-color: #4CAF50;
}

.services-subtitle {
  font-size: 1.2rem;
  color: #555;
  margin-bottom: 2rem;
}

.services-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

.service-card {
  background-color: #f9f9f9;
  border-radius: 8px;
  padding: 1.5rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  text-align: center;
}

.service-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

.service-icon {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.service-card h3 {
  font-size: 1.3rem;
  color: #2E7D32;
  margin-bottom: 0.75rem;
  font-weight: 600;
}

.service-card p {
  color: #666;
  font-size: 0.95rem;
}

.services-footer {
  text-align: center;
  margin-top: 2rem;
  padding: 2rem;
  background-color: #f8f9fa;
  border-radius: 8px;
}

.cta-text {
  font-weight: bold;
  color: #2c6e49;
  margin-left: 0.5rem;
}

.quote-button {
  display: inline-block;
  margin-top: 1.5rem;
  padding: 0.75rem 2rem;
  font-size: 1rem;
  font-weight: 600;
  color: white;
  background-color: #2c6e49;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  text-decoration: none;
}

.quote-button:hover {
  background-color: #214d35;
  text-decoration: none;
  color: white;
}

.cta-section {
  text-align: center;
  padding: 3rem 2rem;
  background-color: #f5f5f5;
  border-radius: 12px;
  margin-top: 3rem;
}

.cta-section h2 {
  text-align: center;
}

.cta-section h2:after {
  left: 50%;
  transform: translateX(-50%);
}

.cta-button {
  background-color: #4CAF50;
  color: white;
  border: none;
  padding: 14px 28px;
  font-size: 1.1rem;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  font-weight: 600;
  display: inline-block;
  text-decoration: none;
  margin-top: 1.5rem;
}

.cta-button:hover {
  background-color: #388E3C;
  color: white;
  text-decoration: none;
}

/* Responsive adjustments */
@media (max-width: 992px) {
  .services-grid {
    grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
  }
}

@media (max-width: 768px) {
  .services-hero {
    height: 200px;
  }
  
  .services-hero h1 {
    font-size: 2.5rem;
  }
  
  .services-grid {
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  }
  
  .services-section h2 {
    font-size: 1.75rem;
  }
}

@media (max-width: 480px) {
  .services-grid {
    grid-template-columns: 1fr;
  }
  
  .cta-button {
    width: 100%;
    padding: 12px;
  }
}
