/* Import Raleway font to match other pages */
@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;500;600;700&display=swap');

.contact-page {
    font-family: 'Raleway', sans-serif;
    color: #333;
    line-height: 1.6;
}

.contact-hero {
    position: relative;
    height: 300px;
    background-image: url('../images/contact.jpg');
    background-size: cover;
    background-position: center;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 2rem;
}

/* Green gradient fallback if image isn't available */
.contact-hero:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(135deg, #4CAF50, #2E7D32);
    z-index: -1;
}

.hero-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4);
    z-index: -1;
}

.contact-hero h1 {
    font-size: 3rem;
    margin-bottom: 20px;
    font-family: 'Raleway', sans-serif;
    font-weight: 700;
    color: #e1ffe2;
}

.contact-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 2rem 1rem 4rem;
}

.contact-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 3rem;
}

.form-section, .contact-info-section {
    margin-bottom: 2rem;
}

.form-section h2, .contact-info-section h2 {
    font-size: 2rem;
    color: #2E7D32;
    margin-bottom: 1rem;
    position: relative;
    padding-bottom: 10px;
}

.form-section h2:after, .contact-info-section h2:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 60px;
    height: 3px;
    background-color: #4CAF50;
}

.contact-form {
    margin-top: 2rem;
}

.form-group {
    margin-bottom: 1.5rem;
}

.form-group label {
    display: block;
    margin-bottom: 0.5rem;
    font-weight: 600;
    color: #444;
}

.form-group input, .form-group select, .form-group textarea {
    width: 100%;
    padding: 12px;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-family: 'Raleway', sans-serif;
    font-size: 1rem;
    transition: border-color 0.3s ease;
}

.form-group input:focus, .form-group select:focus, .form-group textarea:focus {
    outline: none;
    border-color: #4CAF50;
    box-shadow: 0 0 0 3px rgba(76, 175, 80, 0.1);
}

.form-group textarea {
    min-height: 150px;
    resize: vertical;
}

.contact-button {
    background-color: #4CAF50;
    color: white;
    border: none;
    padding: 14px 28px;
    font-size: 1.1rem;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    font-weight: 600;
    display: inline-block;
}

.contact-button:hover {
    background-color: #388E3C;
}

/* Contact information styling */
.info-card {
    background-color: #f9f9f9;
    border-radius: 8px;
    padding: 1.5rem;
    margin-bottom: 1.5rem;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
}

.info-icon {
    font-size: 2rem;
    margin-bottom: 1rem;
}

.info-card h3 {
    font-size: 1.2rem;
    color: #2E7D32;
    margin-bottom: 0.5rem;
}

.business-hours {
    margin-top: 2rem;
    padding: 1.5rem;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
}

.business-hours h3 {
    color: #2E7D32;
    margin-bottom: 1rem;
}

/* Responsive adjustments */
@media (max-width: 992px) {
    .contact-grid {
        grid-template-columns: 1fr;
    }
    
    .contact-info-section {
        margin-top: 2rem;
    }
}

@media (max-width: 768px) {
    .contact-hero {
        height: 200px;
    }
    
    .contact-hero h1 {
        font-size: 2.5rem;
    }
}

@media (max-width: 480px) {
    .form-section h2, .contact-info-section h2 {
        font-size: 1.75rem;
    }
    
    .contact-button {
        width: 100%;
        padding: 12px;
    }
}

/* Add these to your existing CSS file */

.success-message {
    background-color: rgba(76, 175, 80, 0.1);
    border-left: 4px solid #4CAF50;
    padding: 15px;
    margin-bottom: 20px;
    border-radius: 4px;
}

.error-message {
    background-color: rgba(244, 67, 54, 0.1);
    border-left: 4px solid #F44336;
    padding: 15px;
    margin-bottom: 20px;
    border-radius: 4px;
    color: #d32f2f;
}

button:disabled {
    opacity: 0.6;
    cursor: not-allowed;
}
