.hero-content {
  max-width: 800px;
  padding: 0 20px;
}

.hero-content h1 {
  font-size: 3rem;
  margin-bottom: 20px;
  font-family: 'Raleway', sans-serif;
  font-weight: 700;
  color: #e1ffe2;
}

.tagline {
  font-size: 1.5rem;
  margin-bottom: 40px;
  font-weight: 300;
}

.tagline-b {
    color: white;
    font-size: 1.5rem;
    margin-top: 60px;
    font-weight: bold;
  }

.cta-buttons {
  display: flex;
  justify-content: center;
  gap: 20px;
}

/* Buttons */
.btn {
  display: inline-block;
  padding: 12px 30px;
  border-radius: 4px;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
  font-family: 'Raleway', sans-serif;
}

.btn-primary {
  background-color: #4CAF50;
  color: white;
}

.btn-primary:hover {
  background-color: #c4fcc6;
}

.btn-secondary {
  background-color: transparent;
  color: white;
  border: 2px solid white;
}

.btn-secondary:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.btn-outline {
  border: 2px solid #4CAF50;
  color: #4CAF50;
  margin-top: 30px;
}

.btn-outline:hover {
  background-color: #4CAF50;
  color: white;
}

/* Section styling */
section {
  padding: 60px 0;
  font-family: 'Raleway', sans-serif;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

section h2 {
  font-size: 2rem;
  color: #2E7D32;
  margin-bottom: 1.5rem;
  position: relative;
  padding-bottom: 10px;
}

section h2:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 60px;
  height: 3px;
  background-color: #4CAF50;
}

/* Services section */
.home-services-section {
  padding: 80px 0;
  background-color: var(--light-bg);
}

.services-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 30px;
  margin-top: 40px;
}

.service-card {
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  padding: 30px 20px;
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.service-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.15);
}

.service-image {
  width: 100%;
  height: 180px;
  object-fit: cover;
  border-radius: 6px;
  margin-bottom: 20px;
}

.service-icon {
  width: 60px;
  height: 60px;
  margin: 0 auto 20px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.text-center {
  text-align: center;
}

/* Features section */
.features-section {
  padding: 80px 0;
  background-color: #f8fdf8;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 40px;
}

.feature-item {
  text-align: center;
}

.feature-image {
  width: 100%;
  height: 260px;
  object-fit: cover;
  border-radius: 6px;
  margin-bottom: 20px;
}

.feature-icon {
  height: 60px;
  margin-bottom: 20px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

/* Testimonials section */
.testimonials-section {
  padding: 80px 0;
  background-color: #f8f8f8;
  color: #333;
}

.testimonial-carousel {
  position: relative;
  max-width: 800px;
  margin: 0 auto;
  display: flex;
  align-items: center;
}

.testimonial-slider {
  width: 100%;
  height: 200px;
  position: relative;
  overflow: hidden;
}

.testimonial {
  position: absolute;
  width: 100%;
  text-align: center;
  transition: transform 0.5s ease;
}

.testimonial p {
  font-size: 1.5rem;
  font-style: italic;
  margin-bottom: 20px;
  color: #333;
}

.client-name {
  font-weight: 600;
  color: #4CAF50;
}

.carousel-arrow {
  background-color: #4CAF50;
  color: white;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: none;
  font-size: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 2;
  transition: all 0.3s ease;
}

.carousel-arrow:hover {
  background-color: #3d8b40;
  transform: scale(1.1);
}

.prev-arrow {
  margin-right: 15px;
}

.next-arrow {
  margin-left: 15px;
}

.carousel-dots {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #ddd;
  margin: 0 5px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.dot.active {
  background-color: #4CAF50;
  transform: scale(1.2);
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .hero-content h1 {
    font-size: 2.5rem;
  }
  
  .tagline {
    font-size: 1.2rem;
  }
  
  .cta-buttons {
    flex-direction: column;
    gap: 15px;
  }

  .services-grid {
    grid-template-columns: 1fr;
  }

  .carousel-arrow {
    width: 30px;
    height: 30px;
    font-size: 1rem;
  }
  
  .testimonial p {
    font-size: 1.2rem;
  }
  
  .testimonial-slider {
    height: 220px;
  }
}
