/* Main Navigation Styles */
.navigation {
  display: flex;
  align-items: center;
  background: transparent; /* Make nav background transparent to show header gradient */
  padding: 0; /* Remove padding that creates separation */
}

.navigation ul {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.navigation li {
  margin: 0 0.3rem;
  padding: 0;
  position: relative;
}

.navigation a {
  display: block;
  padding: 0.8rem 1.2rem;
  color: #333;
  text-decoration: none;
  font-weight: 500;
  letter-spacing: 0.5px;
  transition: all 0.3s ease;
  position: relative;
  border-radius: 4px;
}

.navigation a::after {
  content: '';
  position: absolute;
  width: 0;
  height: 2px;
  bottom: 0.5rem;
  left: 50%;
  background-color: #4CAF50;
  transition: all 0.3s ease;
  transform: translateX(-50%);
}

.navigation a:hover {
  color: #4CAF50;
  /* Removed background-color */
}

.navigation a:hover::after {
  width: 60%;
}

.navigation a.active {
  color: #4CAF50;
  font-weight: 600;
  /* Removed background-color */
}

.navigation a.active::after {
  width: 70%;
}

/* Mobile Navigation */
.mobile-nav-toggle {
  display: none; /* Keep this display none and remove the duplicate display flex */
  background: transparent;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  color: #333;
  position: relative;
  width: 40px;
  height: 40px;
  padding: 8px;
  margin-left: auto;
  z-index: 1001;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}

.mobile-nav-toggle:hover {
  color: #4CAF50;
}

.mobile-nav-toggle.active {
  color: #4CAF50;
}

/* Remove the custom bar styles since we'll use FontAwesome */
.mobile-nav-toggle .bar {
  display: none;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .mobile-nav-toggle {
    display: flex; /* Now this correctly only applies on mobile */
  }

  .navigation {
    position: fixed;
    top: 0;
    left: -100%;
    height: 100vh;
    width: 80%;
    max-width: 300px;
    background: white; /* Override gradient for mobile menu */
    z-index: 1000;
    flex-direction: column;
    justify-content: flex-start;
    padding-top: 5rem;
    transition: left 0.3s ease;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }

  .navigation.active {
    left: 0;
  }

  .navigation ul {
    flex-direction: column;
    width: 100%;
  }

  .navigation li {
    width: 100%;
    border-bottom: 1px solid #f0f0f0;
  }

  .navigation a {
    padding: 1rem 2rem;
    width: 100%;
  }

  .close-nav {
    display: block; /* Only show on mobile */
    position: absolute;
    top: 1rem;
    right: 1rem;
    background: transparent;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
    color: #333;
    transition: color 0.3s ease;
  }

  .close-nav:hover {
    color: #4CAF50;
  }
}

/* Hide close button on large screens */
.close-nav {
  display: none; /* Hide by default */
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: transparent;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  color: #333;
  transition: color 0.3s ease;
}

/* Animation for mobile menu */
@keyframes slideIn {
  from {
    left: -100%;
  }
  to {
    left: 0;
  }
}

@keyframes slideOut {
  from {
    left: 0;
  }
  to {
    left: -100%;
  }
}

.slide-in {
  animation: slideIn 0.3s forwards;
}

.slide-out {
  animation: slideOut 0.3s forwards;
}

/* Overlay for mobile navigation */
.mobile-nav-overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 999;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.mobile-nav-overlay.active {
  display: block;
  opacity: 1;
}
